import { useState } from "react";
import {
  FormControlLabel,
  Button,
  Input,
  Select,
  MenuItem,
  Checkbox,
} from "@material-ui/core";
import StyledRegister from "./style";

function Register() {
  const [subject, setSubject] = useState();
  const [exp, setExp] = useState();
  const [time, setTime] = useState();
  const [ntTerm, setNtTerm] = useState();
  const [ntType, setNtType] = useState("day");
  const [share, setShare] = useState(false);

  return (
    <StyledRegister>
      <form className="form" action="/submit" method="post">
        <Input
          name="subject"
          type="text"
          placeholder="목표"
          value={subject}
          onChange={(e) => {
            setSubject(e.target.value);
          }}
        />
        <Input
          name="exp"
          type="date"
          placeholder="종료일자"
          value={exp}
          onChange={(e) => {
            setExp(e.target.value);
          }}
        />
        <Input
          name="time"
          type="time"
          placeholder="종료시간"
          value={time}
          onChange={(e) => {
            setTime(e.target.value);
          }}
        />
        <div className="select-row">
          {(ntType === "time" || ntType === "day") && (
            <Input
              name="ntTerm"
              className="select-value"
              type="number"
              value={ntTerm}
              onChange={(e) => {
                setNtTerm(Number(e.target.value));
              }}
            />
          )}
          {ntType === "days" && (
            <Select
              name="ntTerm"
              className="select-type"
              value={ntTerm}
              onChange={(e) => setNtTerm(Number(e.target.value))}
            >
              <MenuItem value="0">일</MenuItem>
              <MenuItem value="1">월</MenuItem>
              <MenuItem value="2">화</MenuItem>
              <MenuItem value="3">수</MenuItem>
              <MenuItem value="4">목</MenuItem>
              <MenuItem value="5">금</MenuItem>
              <MenuItem value="6">토</MenuItem>
            </Select>
          )}
          <Select
            name="ntType"
            className="select-type"
            value={ntType}
            onChange={(e) => setNtType(e.target.value)}
          >
            <MenuItem value="day">일</MenuItem>
            <MenuItem value="days">요일</MenuItem>
            <MenuItem value="time">시간</MenuItem>
            <MenuItem value="once">한번</MenuItem>
          </Select>
          <div className="select-text">
            {ntType === "once" ? "만 알림" : "마다 알림"}
          </div>
        </div>
        <FormControlLabel
          control={
            <Checkbox
              name="share"
              value={share}
              color="primary"
              checked={share}
              onChange={(e) => {
                setShare(e.target.checked);
              }}
            />
          }
          label="공유하기"
        />
        <div className="button-wrapper">
          <Button variant="contained" color="primary" type="submit">
            등록
          </Button>
        </div>
      </form>
    </StyledRegister>
  );
}

export default Register;
