import styled from "styled-components";

const Register = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 10vh;

  .form {
    display: flex column;
    font-size: 1.2rem;
    width: 20rem;
    & > div {
      width: 100%;
      margin: 12px 0;
    }

    .button-wrapper {
      display: flex;
      justify-content: flex-end;
    }
  }

  .select-row {
    display: flex;
    justify-content: space-between;

    .select-value {
      width: 30%;

      & > input {
        text-align: right;
      }
    }

    .select-type {
      width: 35%;

      & > div {
        text-align: right;
      }
    }

    .select-text {
      width: 20%;
      text-align: center;
      align-self: center;
    }
  }
`;
export default Register;
